@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@400;500;600&display=swap);
body,
html {
  margin: 0;
  padding: 0;
  font-family: "Mulish", sans-serif;
  background-color: #262223;
  height: 100vh;
  width: 100%;
}

code {
  font-family: "Satisfy", cursive;
}

.home__container {
  height: 100%;
  width: 100%;
}

.scrolltotop__container {
  width: 50px;
  height: 50px;
  align-items: center;
  justify-content: center;
  flex-flow: column nowrap;
  position: fixed;
  bottom: 0.5rem;
  right: 0.5rem;
  background-color: #262223;
  border-radius: 50%;
}

.scrolltotop__btn {
  width: 100%;
  height: 100%;
  color: #f2f2f2;
  cursor: pointer;
  -webkit-animation: fadeIn 0.3s;
          animation: fadeIn 0.3s;
  transition: opacity 0.4s;
}

.scrolltotop__btn:hover {
  opacity: 1;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.7;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.7;
  }
}

.landinghero__highlightcolor {
  color: #f2811d;
}

.landinghero__landingcontainer {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-flow: row wrap;
}

.landinghero__landingleftcontainer {
  height: 100%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

.landinghero__landingrightcontainer {
  height: 100%;
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column nowrap;
  position: relative;
}

.landinghero__landingleftcontainer h1 {
  color: #f2f2f2;
  font-size: 10em;
  font-style: normal;
  margin: 0;
  font-weight: 600;
}

.landinghero__landingleftcontainer h2:nth-of-type(1) {
  color: #f2f2f2;
  font-size: 3em;
  font-style: normal;
  margin: 0;
  margin-left: 10px;
  font-weight: 400;
}

.landinghero__landingleftcontainer h2:nth-of-type(2) {
  color: #f2f2f2;
  font-size: 5em;
  font-style: normal;
  margin: 0;
  font-weight: 500;
}

.landinghero__landingrightcontainer .landinghero__interesttop {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-flow: row nowrap;
}

.landinghero__landingrightcontainer .landinghero__interestbottom {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-flow: row nowrap;
}

.landinghero__landingrightcontainer .landinghero__scrollwrapper {
  width: 100%;
  height: 100px;
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  flex-flow: column nowrap;
  position: absolute;
  bottom: 0;
  padding-bottom: 10px;
}

.landinghero__landingrightcontainer
  .landinghero__scrollwrapper
  .landinghero__scrollmouse {
  height: 42px;
  width: 24px;
  border-radius: 14px;
  transform: none;
  border: 2px solid #f2f2f2;
  margin-top: 5px;
}

.landinghero__landingrightcontainer
  .landinghero__scrollwrapper
  .landinghero__scrollmousewheel {
  height: 5px;
  width: 2px;
  display: block;
  margin: 5px auto;
  background: #f2f2f2;
  position: relative;
  height: 4px;
  width: 4px;
  border: 2px solid #f2f2f2;
  border-radius: 8px;
  -webkit-animation: mouse-wheel 0.6s linear infinite;
          animation: mouse-wheel 0.6s linear infinite;
}

@-webkit-keyframes mouse-wheel {
  0% {
    top: 1px;
  }
  25% {
    top: 2px;
  }
  50% {
    top: 3px;
  }
  75% {
    top: 2px;
  }
  100% {
    top: 1px;
  }
}

@keyframes mouse-wheel {
  0% {
    top: 1px;
  }
  25% {
    top: 2px;
  }
  50% {
    top: 3px;
  }
  75% {
    top: 2px;
  }
  100% {
    top: 1px;
  }
}

.landinghero__scrollarrows {
  display: block;
  width: 5px;
  height: 5px;
  transform: rotate(45deg);

  border-right: 1px solid #f2f2f2;
  border-bottom: 1px solid #f2f2f2;
  margin: 0 0 0 5px;

  width: 16px;
  height: 16px;
}

.scrollarrows__1,
.scrollarrows__2,
.scrollarrows__3 {
  -webkit-animation: mouse-scroll 1s infinite;
          animation: mouse-scroll 1s infinite;
}

.scrollarrows__1 {
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-delay: alternate;
          animation-delay: alternate;
  margin-top: 1px;
}

.scrollarrows__2 {
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
  margin-top: -6px;
}

.scrollarrows__3 {
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
  margin-top: -6px;
}

@-webkit-keyframes mouse-scroll {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
    border-right: 2px solid #f2f2f2;
    border-bottom: 2px solid #f2f2f2;
  }
  100% {
    opacity: 1;
    border-right: 2px solid #f2811d;
    border-bottom: 2px solid #f2811d;
  }
}

@keyframes mouse-scroll {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
    border-right: 2px solid #f2f2f2;
    border-bottom: 2px solid #f2f2f2;
  }
  100% {
    opacity: 1;
    border-right: 2px solid #f2811d;
    border-bottom: 2px solid #f2811d;
  }
}

@media only screen and (max-width: 768px) {
  .landinghero__landingleftcontainer {
    align-items: center;
    justify-content: center;
    height: 30%;
    width: 100%;
  }

  .landinghero__landingleftcontainer h1 {
    font-size: 5em;
  }

  .landinghero__landingleftcontainer h2:nth-of-type(1) {
    font-size: 1.3em;
  }

  .landinghero__landingleftcontainer h2:nth-of-type(2) {
    font-size: 3em;
  }

  .landinghero__landingrightcontainer {
    width: 100%;
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .landinghero__landingrightcontainer .landinghero__interesttop {
    flex-flow: column nowrap;
  }

  .landinghero__landingrightcontainer .landinghero__interestbottom {
    flex-flow: column nowrap;
  }

  .landinghero__landingrightcontainer .landinghero__scrollwrapper {
    display: none;
  }
}

.landingherointerest__wrapper {
  width: 250px;
  height: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: row nowrap;
  position: relative;
}

.landingherointerest__wrapper .landingherointerest__interest {
  width: 150px;
  height: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: row nowrap;
  background: #262223;
  border-radius: 50%;
  box-shadow: 0px 0px 12px rgba(242, 129, 29, 0.2);
  z-index: 1;
}

.landingherointerest__wrapper .landingherointerest__interest h2 {
  color: #f2f2f2;
  font-size: 1.5em;
  font-style: normal;
  margin: 0;
  font-weight: 400;
}

.landingherointerest__wrapper .landingherointerest__animationcontainerbox {
  width: 250px;
  height: 250px;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
}

.landingherointerest__wrapper .landingherointerest__animationcontainerbox span {
  position: absolute;
  box-sizing: border-box;
  border: none;
  background: rgba(242, 129, 29, 0.25);
  border-radius: 50%;
  -webkit-animation: animate 5s linear infinite;
          animation: animate 5s linear infinite;
  -webkit-animation-delay: calc(-0.5s * var(--i));
          animation-delay: calc(-0.5s * var(--i));
}

@-webkit-keyframes animate {
  0% {
    width: 0px;
    height: 0px;
  }
  50% {
    opacity: 1;
  }
  100% {
    width: 250px;
    height: 250px;
    opacity: 0;
  }
}

@keyframes animate {
  0% {
    width: 0px;
    height: 0px;
  }
  50% {
    opacity: 1;
  }
  100% {
    width: 250px;
    height: 250px;
    opacity: 0;
  }
}

@media only screen and (max-width: 768px) {
  .landingherointerest__wrapper {
    width: 200px;
    height: 200px;
  }

  .landingherointerest__wrapper .landingherointerest__interest {
    width: 100px;
    height: 100px;
  }

  .landingherointerest__wrapper .landingherointerest__interest h2 {
    font-size: 1em;
  }

  .landingherointerest__wrapper .landingherointerest__animationcontainerbox {
    width: 200px;
    height: 200px;
  }

  @-webkit-keyframes animate {
    0% {
      width: 0px;
      height: 0px;
    }
    50% {
      opacity: 1;
    }
    100% {
      width: 200px;
      height: 200px;
      opacity: 0;
    }
  }

  @keyframes animate {
    0% {
      width: 0px;
      height: 0px;
    }
    50% {
      opacity: 1;
    }
    100% {
      width: 200px;
      height: 200px;
      opacity: 0;
    }
  }
}

.abouthero__container {
  width: 100%;
  height: 100vh;
  background-color: #f2811d;
}

.abouthero__titlewrapper {
  width: 100%;
  height: 10%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.abouthero__title {
  color: #262223;
  font-style: normal;
  font-weight: 400;
