:root {
  --duration: 5000ms;
}

.loader__container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column nowrap;
  background-color: #262223;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.loader__title {
  color: #f2f2f2;
  font-style: 2em;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 600;
  -webkit-animation: colorflow 1.3s infinite ease;
          animation: colorflow 1.3s infinite ease;
}

@-webkit-keyframes colorflow {
  0% {
    color: #f2811d;
  }
  100% {
    color: #f2f2f2;
  }
}

@keyframes colorflow {
  0% {
    color: #f2811d;
  }
  100% {
    color: #f2f2f2;
  }
}

.loader__cubegrid {
  width: 40px;
  height: 40px;
}

.loader__cube {
  width: 33%;
  height: 33%;
  background-color: #f2f2f2;
  float: left;
  -webkit-animation: cubegridScaleDelay 1.3s infinite ease-in-out;
          animation: cubegridScaleDelay 1.3s infinite ease-in-out;
}

.loader__cube1 {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

.loader__cube2 {
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
}

.loader__cube3 {
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
}

.loader__cube4 {
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
}

.loader__cube5 {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

.loader__cube6 {
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
}

.loader__cube7 {
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}

.loader__cube8 {
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
}

.loader__cube9 {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

@-webkit-keyframes cubegridScaleDelay {
  0%,
  70%,
  100% {
    transform: scale3d(1, 1, 1);
  }
  35% {
    transform: scale3d(0, 0, 1);
    background-color: #f2811d;
  }
}

@keyframes cubegridScaleDelay {
  0%,
  70%,
  100% {
    transform: scale3d(1, 1, 1);
  }
  35% {
    transform: scale3d(0, 0, 1);
    background-color: #f2811d;
  }
}

.navbar__container {
  width: 100%;
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: row nowrap;
}

.navbar__container .navbar__menubtnwrapper {
  width: 39px;
  overflow: hidden;
}

.navbar__menucheckbox {
  display: none;
}

.navbar__menucheckboxlabel {
  position: relative;
  display: block;
  height: 29px;
  cursor: pointer;
  z-index: 3;
}

.navbar__menucheckboxlabel::before,
.navbar__menucheckboxlabel::after,
.navbar__menucheckboxbars {
  position: absolute;
  left: 0;
  width: 100%;
  height: 5px;
  background: #f2f2f2;
}

.navbar__menucheckboxlabel::before,
.navbar__menucheckboxlabel::after {
  content: "";
  transition: 0.4s cubic-bezier(0.68, -0.55, 0.27, 1.55) left;
}

.navbar__menucheckboxlabel::before {
  top: 0;
}

.navbar__menucheckboxlabel::after {
  top: 12px;
}

.navbar__menucheckboxbars {
  top: 24px;
}

.navbar__menucheckboxbars::before {
  content: "CLOSE";
  position: absolute;
  top: 5px;
  right: 0;
  left: 0;
  color: #f2f2f2;
  font-size: 12px;
  font-weight: 400;
  text-align: center;
}

.navbar__menucheckbox:checked + .navbar__menucheckboxlabel::before {
  left: -39px;
}

.navbar__menucheckbox:checked + .navbar__menucheckboxlabel::after {
  left: 39px;
}

.navbar__menucheckbox:checked
  + .navbar__menucheckboxlabel
  .navbar__menucheckboxbars::before {
  -webkit-animation: moveUpThenDown 0.8s ease forwards,
    shakeWhileMovingUp 0.8s ease 0.2s forwards,
    shakeWhileMovingDown 0.2s ease 0.8s forwards;
          animation: moveUpThenDown 0.8s ease forwards,
    shakeWhileMovingUp 0.8s ease 0.2s forwards,
    shakeWhileMovingDown 0.2s ease 0.8s forwards;
}

@-webkit-keyframes moveUpThenDown {
  0% {
    top: 0;
  }
  50% {
    top: -27px;
  }
  100% {
    top: -14px;
  }
}

@keyframes moveUpThenDown {
  0% {
    top: 0;
  }
  50% {
    top: -27px;
  }
  100% {
    top: -14px;
  }
}

@-webkit-keyframes shakeWhileMovingUp {
  0% {
    transform: rotateZ(0);
  }
  25% {
    transform: rotateZ(-5deg);
  }
  50% {
    transform: rotateZ(0deg);
  }
  75% {
    transform: rotateZ(5deg);
  }
  100% {
    transform: rotateZ(0);
  }
}

@keyframes shakeWhileMovingUp {
  0% {
    transform: rotateZ(0);
  }
  25% {
    transform: rotateZ(-5deg);
  }
  50% {
    transform: rotateZ(0deg);
  }
  75% {
    transform: rotateZ(5deg);
  }
  100% {
    transform: rotateZ(0);
  }
}

@-webkit-keyframes shakeWhileMovingDown {
  0% {
    transform: rotateZ(0);
  }
  80% {
    transform: rotateZ(3deg);
  }
  90% {
    transform: rotateZ(-3deg);
  }
  100% {
    transform: rotateZ(0);
  }
}

@keyframes shakeWhileMovingDown {
  0% {
    transform: rotateZ(0);
  }
  80% {
    transform: rotateZ(3deg);
  }
  90% {
    transform: rotateZ(-3deg);
  }
  100% {
    transform: rotateZ(0);
  }
}

